import React from 'react';
import { Parallax } from "react-parallax";
class UDParallax extends React.Component {

  // state is for keeping control state before or after changes.
  state = {

    // the things you want to put in state.
    // text: this.props.text //un comment the line to use state insted props
  }


  render() {
const styles = {
  fontFamily: "sans-serif",
  textAlign: "center"
};
const insideStyles = {
  background: this.props.background,
  padding: this.props.padding,
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%,-50%)",
  fontSize: this.props.fontSize,
  color: this.props.textColor
};
      // These props are returned from PowerShell!
      // return <h1>{this.state.text}</h1> // un comment the line to render using value from state.

    return (
        <div style={styles}>
    <Parallax bgImage={this.props.bgImage} blur={{ min: this.props.blurMin, max: this.props.blurMax }} strength={this.props.strength}>
      <div style={{ height: this.props.height }}>
            <div style={insideStyles}>{this.props.text}</div>
          </div>
        </Parallax>
        </div>
    );

  }
}

export default UDParallax
